.fn1-btn {
    background: linear-gradient(to bottom, #f2f2f9 21%,#CDD1D9 80%);
    border:1px solid $color-border-input;
    border-radius:.4em;
    box-sizing: content-box;
    color: $color-heading-2;
    display:inline-block;
    line-height: 1em;
    padding: .5em 1em;
    font-size: (14rem/16);
    font-weight:normal;
    vertical-align: middle;

    &:hover {
    background: linear-gradient(to bottom, #f8f8fd 21%, #d5dfe4 80%);
    cursor: pointer;
    text-decoration: none;
    }
    &-action {
        @extend .fn1-btn;
    }
    &-featuredAction {
        @extend .fn1-btn-action;
        font-size: (24rem/16);
    }
    &-container {
            @include clearfix();
            margin: 1em 0;
        &--centered {
            @extend .fn1-btn-container;
            text-align: center;
        }
        > [class^=fn1-btn] {
            margin: 0 1em 0 0;
        }
    }
}
